.mentors{
    width: 100%;
    h3.title{
        text-align: center;
    }

    .owl-carousel{
        margin-top: 50px;
        .cards{
            position: relative;
            img{
                border-radius: 5px;
            }
            .textCard{
                position: absolute;
                bottom: 0px;
                left: 0px;
                width: 100%;
                background-color: black;
                height: auto;
                padding: 10px 5px;
                border-bottom-left-radius: 5px;
                border-bottom-right-radius: 5px;
                h2{
                    font-size: 14px;
                    font-weight: 700;
                    color: white;
                    text-align: center;
                }
                p{
                    color: #fbfbfb;
                    text-align: center;
                    font-size: 12px;
                }
            }
        }
    }
}