section#atmofera {
    width: 100%;
    margin-top: 100px;

    .videos {
        margin-top: 50px;
        position: relative;
        height: 500px;

        .text {
            position: absolute;
            top: 50%;
            z-index: 1111;
            transform: translateY(-50%);
            left: 200px;

            .sm-block({
                left: 20px;
                // width: 100%;.
            })
        }

        p {
            max-width: 600px;
            margin-bottom: 20px;

            span {
                font-size: 50px;
                display: block;
                color: #fff;
                font-weight: 900;
                margin-bottom: 20px;
                letter-spacing: 2px;

                .sm-block({
                    font-size: 30px;
                })
            }

            .smart {
                color: #30a8ff;
                font-size: 30px;
                font-weight: 900;
                display: inline-block;
                margin-bottom: 0;
            }

            line-height: 40px;
            left: 200px;
            color: white;
            font-size: 30px;

            .sm-block({
                width: 100%;
                max-width: unset;
            })

        }

        &::after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0px;
            left: 0px;
            background: rgba(0, 0, 0, .7);
        }

        video {
            height: 100%;
            width: 100%;
        }

        h3.title {
            position: absolute;
            top: 50%;
            right: 30%;
        }
    }
}