.price {
    padding: 50px 0;
    margin-top: 100px;
    background-color: #fafafa;
    .title {
        text-align: center;
    }

    .price-box-title {
        background-color: #000;
        color: #fff;
        padding: 0px 35px;
        border-radius: 5px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        p {
            line-height: 28px;
        }

        span {
            color: #30a8ff;
            font-weight: 700;
        }
        strong {
            color: red;
        }

        .sale {
            position: absolute;
            top: 20px;
            right: -42px;
            display: flex;
            // align-items: center;
            // justify-content: center;
            padding: 15px 5px 15px 10px;
            font-weight: 500;
            width: 104px;
            font-size: 14px;
            letter-spacing: 2px;
            background: linear-gradient(to right, red 0% 59%,  #000 59% 100%);
            color: #fff;
            clip-path: polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%);
            text-align: center;
                

            i {
                margin-left: 5px;
            }

            .md-block({
                transform: rotate(90deg);
                top: unset;
                right: 0;
                bottom: -13px;
            })
        }

        .md-block({
            padding: 50px 10px;
        })
    }

    .price-box {
        margin-bottom: 15px;

        .check-icon {
            font-size: 30px;
            font-weight: 900;
            margin-bottom: 20px;
            color: #30a8ff;
        }

        p {
            line-height: 28px;
        }

        span {
            color: red;
            font-weight: 700;
        }

        .md-block({
            margin-bottom: 0;
            margin-top: 25px;
        })
    }
}
