footer {
    width: 100%;
    padding-top: 100px;
    background: url("../images/bgFooter.jpg") no-repeat center / cover;
    background-attachment: fixed;
    margin-top: 100px;
    text-align: center;
    color: white;
    position: relative;

    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .7);
        position: absolute;
        top: 0px;
        left: 0px;
    }

    .footerTop {
        position: relative;
        z-index: 1111;

        form {
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            width: 100%;
            margin-top: 50px;

            input,
            button {
                width: 800px;
                padding: 20px 40px;
                background-color: transparent;
                border: 2px solid white;
                font-size: 16px;
                font-weight: 700;
                color: white;
                border-radius: 5px;
                margin: auto;
                margin-bottom: 20px;
                letter-spacing: 2px;
                &:focus{
                    outline: none;
                }

                .md-block({
                    width: 80%;
                })
            }

            button {
                background-color: #30a8ff;
                border: none;

                &:after {
                    display: none;
                }

                &::before {
                    display: none;
                }
            }
        }


    }

    nav {
        display: flex;
        justify-content: space-between;
        position: relative;
        z-index: 1111;
        background-color: black;
        align-items: center;
        margin-top: 50px;
        padding: 30px 0px;

        ul {
            display: flex;

            li {
                margin-left: 20px;
                transition: 1s;

                &:hover {

                    transform: rotate(360deg) scale(1.2);
                }

                a {
                    color: white;
                    font-size: 24px;
                    transition: 1s;
                    cursor: pointer;

                    &:hover {
                        color: #30a8ff;
                        transform: rotate(360deg);
                    }
                }
            }
        }
    }
}


