section#learn {
    width: 100%;
    text-align: center;
    margin: 100px 0px;
    background-color: #fafafa;
    padding-top: 100px;

    .bgCard {
        background-color: #fafafa;
    }

    
    
    .img {
        position: relative;
        margin-left: -15px;
        margin-right: -15px;
        
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            background-color: #8ad2ff;
            opacity: .6;
            z-index: 11;
        }
    }
    
    .card {
        background-color: rgba(48, 168, 255, .6);
        margin: 0px -15px;
        height: 287px;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        
        h2 {
            text-align: left;
            font-size: 20px;
            line-height: 28px;
            margin-bottom: 10px;
        }
        
        button.bgBlue {
            background-color: black !important;
            color: white;
        }
    }
    
    .cards {
        h3 {
            text-align: left;
            margin-bottom: 10px;
            font-size: 20px;
            font-family: 'Inter', sans-serif;
            color: rgba(102, 102, 102, 1);
            padding-top: 30px;
            
            span {
                color: #8ad2ff;
                font-size: 24px;
                margin-right: 10px;
            }
        }
        
        p {
            text-align: left;
            font-size: 15px;
            line-height: 28px;
        }
    }

    .md-block({
    
       .learn-md-box:nth-child(7),
            .learn-md-box:nth-child(6),
            .learn-md-box:nth-child(3),
            .learn-md-box:nth-child(2) {
        
                .col-lg-6:nth-of-type(1) {
                    order: 2;
                }
        
                .col-lg-6:nth-of-type(2) {
                    order: 1;
                }
            }
    })
}


@media screen and (max-width: 576px) {
    .bgCard {
        display: grid;

        .cards {
            margin: 10px 0 30px 0;
        }
    }

    .learn-md-box:nth-child(6),
    .learn-md-box:nth-child(2) {

        .col-lg-6:nth-of-type(1) {
            grid-area: 1;
        }

        .col-lg-6:nth-of-type(2) {
            grid-area: 2;
        }
    }

    .learn-md-box:nth-child(4){

        .col-lg-6:nth-of-type(1) {
            grid-area: 2;
        }

        .col-lg-6:nth-of-type(1) {
            grid-area: 2;
        }
    }

}