section.benifits{
    width: 100%;
    margin-top: 100px;
    background-color: #fafafa;
    padding: 100px 0px;
    h3.title{
        margin-bottom: 50px;
        text-align: center;
    }

    button.bgBlue{
        margin-left: 35px;
        margin-top: 30px;
        padding: 20px 40px ;
    }

    h4{
        text-align: left;
        max-width: 660px;
        margin: auto;
        font-size: 20px;
        font-weight: 500;
        line-height: 32px;
        margin-top: 15px;
        span{
            color: #001bff;
        }
    }
    .cards{
        width: 100%;
        height: 100%;
        padding: 50px;
        background-color: #000;
        color: white;
        height: 300px;
        margin: 15px 0px;   
        border-radius: 5px;
         svg{
            width: 50px;
            height: 50px;
            fill: #8ad2ff;
            margin-bottom: 30px;
        }
    }
}