header {
    width: 100%;

    nav {
        width: 100%;
        padding: 20px 0px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: rgb(24, 24, 24);
        position: fixed;
        top: 0px;
        left: 0px;
        right: 0;
        z-index: 11111;



        ul {
            display: flex;
            align-items: center;

            li {
                margin-left: 30px;
                overflow: hidden;
                position: relative;

                &:last-of-type {
                    margin-right: 30px;
                }

                .call {
                    &::after {
                        display: none;
                    }

                    &::before {
                        display: none;
                    }
                }
            }

            a {
                color: white;
                font-size: 14px;
                font-weight: 500;
                letter-spacing: 0.2px;
                padding: 5px 0px;
                display: block;

                .fa {
                    margin-right: 5px;
                }

                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    top: 0px;
                    left: -200px;
                    background-color: #30a8ff;
                    transition: 1s;
                    cursor: pointer;
                }

                &::before {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    bottom: 0px;
                    right: -200px;
                    background-color: #30a8ff;
                    transition: 1s;
                    cursor: pointer;
                }

                &:hover {
                    &::after {
                        left: 0px;
                    }

                    &::before {
                        right: 0px;
                    }
                }

            }
        }
    }
}

.header-title {
    width: 100%;
    margin-top: 70px;
    position: relative;



    .owl-carousel {
        .owl-nav {
            display: none;

            button {
                color: white;
                font-size: 70px;
                font-weight: 500;
            }
        }

        .owl-dots {
            position: absolute;
            bottom: 50px;
            width: 100%;
            left: 0px;
            text-align: center;
            display: flex;
            justify-content: center;

            .active {
                background-color: #30a8ff;
            }

            button {
                margin: 0px 10px;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: #fff;
            }
        }

        img {
            // height: 700px;
            width: 100%;
            // object-fit: cover;
            // object-position: center;

            .sm-block({
                height: 400px;
            })
        }

        .itemCard {
            position: relative;

            &::after {
                content: "";
                position: absolute;
                top: 0px;
                left: 0px;
                width: 100%;
                height: 100%;
                background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, .4));
                z-index: 111;
            }

            .text {
                position: absolute;
                top: 50%;
                // left: 200px;
                transform: translateY(-50%);
                z-index: 1111;
                color: white;

                h2 {
                    font-size: 35px;
                    font-weight: 500;
                    margin-bottom: 20px;
                    max-width: 800px;
                    line-height: 40px;

                    span {
                        color: #30a8ff;
                    }

                    .sm-block({
                        font-size: 24px;
                    })
                }

                p {
                    max-width: 600px;
                    margin-bottom: 40px;
                }
            }
        }
    }

}

.bars {
    display: none;
    color: #fff;
    font-size: 25px;
}


@media screen and (max-width: 1200px) {
    header{
        
        nav ul li {
            margin-left: 15px;
        }

        .logo-container {
            padding-right: 0;
        }
        
        .bars {
            display: block;
        }

        .navbar {
            position: fixed;
            top: 70px;
            right: 0;
            width: 50%;
            height: 100vh;
            display: flex;
            background-color: #181818;
            right: -100%;
            transition: all 700ms ease-in;

            ul {
                flex-direction: column;
                width: 100%;
                align-items: center;
                // justify-content: center;
                padding-top: 100px;

                li {
                    margin: 7px 0;
                    
                    a{
                        font-size: 20px;
                    }
                }

                .bgBlue {
                    margin:30px 0 0 0;
                    &::after {
                        display: none;
                    }

                    &::before {
                        display: none;
                    }
                }
            }

            
            .sm-block({
                width: 100%;
            })
        }

        .navbar.active {
            right: 0;
        }
    } 
}

@media screen and (max-width:500px) {
    header .itemCard .text {

        h2 {
            font-size: 20px;
        }
    }
}