header {
  width: 100%;
}
header nav {
  width: 100%;
  padding: 20px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #181818;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0;
  z-index: 11111;
}
header nav ul {
  display: flex;
  align-items: center;
}
header nav ul li {
  margin-left: 30px;
  overflow: hidden;
  position: relative;
}
header nav ul li:last-of-type {
  margin-right: 30px;
}
header nav ul li .call::after {
  display: none;
}
header nav ul li .call::before {
  display: none;
}
header nav ul a {
  color: white;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.2px;
  padding: 5px 0px;
  display: block;
}
header nav ul a .fa {
  margin-right: 5px;
}
header nav ul a::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 0px;
  left: -200px;
  background-color: #30a8ff;
  transition: 1s;
  cursor: pointer;
}
header nav ul a::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0px;
  right: -200px;
  background-color: #30a8ff;
  transition: 1s;
  cursor: pointer;
}
header nav ul a:hover::after {
  left: 0px;
}
header nav ul a:hover::before {
  right: 0px;
}
