* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
}
body {
  font-family: 'Inter', sans-serif;
}
a.logo {
  color: white;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 30px;
  transition: 1s;
  cursor: pointer;
}
a.logo:hover {
  color: #30a8ff;
  cursor: pointer;
}
a.logo span {
  color: #30a8ff;
}
img,
video,
svg {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}
.bgBlue {
  padding: 10px 20px;
  background-color: #30a8ff;
  border-radius: 5px;
  font-size: 14px;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
  border: none;
  color: white;
}
.bgBlue span {
  color: white;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.5px;
}
.bgBlue::after {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -50%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 1s;
  cursor: pointer;
}
.bgBlue::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  bottom: 0;
  left: -50%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 1s ;
  cursor: pointer;
}
.bgBlue:hover {
  transition: 1s;
  cursor: pointer;
}
.bgBlue:hover::after {
  right: 0px;
}
.bgBlue:hover::before {
  left: 0px;
}
.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1111111;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0px;
  left: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  display: none;
}
.modal .close {
  color: white;
  position: absolute;
  top: 50px;
  right: 80px;
  font-size: 40px;
  cursor: pointer;
  transition: 1s;
}
.modal .close:hover {
  transform: rotate(360deg);
}
.modal h2 {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 50px;
  color: white;
  display: inline-block;
  border-bottom: 2px solid #30a8ff;
  padding-bottom: 20px;
  width: 800px;
  text-align: center;
}
.modal h2 span {
  color: #30a8ff;
}
.modal form {
  display: flex;
  flex-direction: column;
}
.modal form input {
  width: 700px;
  padding: 20px 30px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  color: white;
  border: 1px solid #dcdcdc;
  background-color: transparent;
  margin-bottom: 30px;
}
.modal form button {
  padding: 20px 0px;
}
.modal form button::after,
.modal form button::before {
  display: none;
}
h3.title {
  font-size: 24px;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  letter-spacing: 1px;
}
.mt-50 {
  margin-top: 50px;
}
.scrollTop {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  bottom: 20px;
  right: 20px;
  background: black;
  color: white;
  display: flex;
